@import './colors';

@keyframes enter {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    } 
}

@keyframes overlay {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.7;
    } 
}

@keyframes overlay-leave {
    0% {
        opacity: 0.7;
    }
    100% {
        opacity: 0;
    } 
}

@keyframes mobile-nav {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
    
}

@keyframes mobile-nav-bg {
    0% {
        width: 6.5rem;
        height: 6.5rem;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
    30% {
        border-top-right-radius: 1rem;
    }
    100% {
        width: 20%;
        height: 60%;
    }
}

@keyframes mobile-nav-bg-tab-port {
    0% {
        width: 6.5rem;
