@import '../../keyframes'; 
@import '../../mixins';

.my-skills__container {
    width: 85%;
    height: 90%;
    display: block;
    animation-name: enter;
    animation-duration: 1s;
    animation-timing-function: ease;
    overflow-y: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    padding: 0 5rem;

    &::-webkit-scrollbar {
        display: none;
    }

    & h1 {
        margin-bottom: 3rem;
    }

    & h3 {
        margin: 2rem 0 0 0;
    }

    @include respond (large-tab-portrait) {
        & {
          justify-content: space-evenly;
        }
    }

    @include respond (phone) {
        & {
           width: 100%;
           height: 100%;
           display: block;          
        }
    }
}

.fa-code.active {
    color: seagreen;
}

.fa-html5.active {
    color: #f58556;
}

.fa-css3-alt.active {
    color: #3A94F6;
}

.fa-js.active {
    color: #f7df1e;
}

.fa-node.active {
    color: #68a063;
}

.fa-react.active {
    color: #61dbfb;
}

.fa-sass.active {
    color: #cc6699;
}

img.active {
    filter: none;
    -webkit-filter: none;
}