@mixin respond($breakpoint) {
    @if $breakpoint == big-desktop {
        @media (min-width: 128em) {@content}; //2048px
    }
    @if $breakpoint == hide-nav {
        @media (max-width: 93.75em) {@content}; //1500px
    }
    @if $breakpoint == tab-land {
        @media (max-width: 75em) {@content}; //1200px
    }
    @if $breakpoint == large-tab-portrait {
        @media (min-width: 50em) and (max-width: 65.63em) and (min-height: 68.75em) and (max-height: 87.5em)  {@content}; //min-width: 800px, max-width: 1050px , max-height: 1400px, min-height: 1100px
    }
    @if $breakpoint == ipad {
        @media (min-height: 56.25em) and (max-height: 68.75em) and (min-width: 43.75em) and (max-width: 65.63em)   {@content}; //min-height: 900px, max-height: 1100px, min-width: 700px, max-width: 1050px 
    }
    @if $breakpoint == tab-port {
        @media (max-width: 56.25em) {@content};  //900px
    }
    @if $breakpoint == phone {
        @media (max-width: 37.5em) {@content}; //600px
    }
    @if $breakpoint == small-phone {
        @media (max-width: 21.88em) {@content}; //350px
    }
}