@import '../../../colors';
@import '../../../mixins';

.skill__container {
    width: 100%;
    max-height: 4rem;
    background: $app-boxes;
    border-radius: 1rem;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    border: 2px solid $light-gray;
    padding: 0 2rem 2rem 2rem;
    margin-bottom: 2rem;
    overflow: hidden;
    transition: all 1s;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -ms-transition: all 1s;
    -o-transition: all 1s;
    &.open {
        max-height: 30rem;
    }

    & .skill__display {
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        margin-bottom: 2rem;

        & h2, & i {
            margin: .5rem 0 0 0;
            line-height: 4rem;
        } 

        & i {
            font-size: 4rem;

            &:hover {
                cursor: pointer;
            }
        }
    }

    & .skill__symbol-text {
        display: flex;
        flex-flow: row nowrap;
        margin: 0;
        padding: 0;
        justify-content: center;
        align-items:center;
        width: 100%;
        height: fit-content;
    }

    & .skill__symbol-container {
        text-align: center;
        width: 15%;

        & h2 {
            width: 100%;
            font-size: 2rem;
            line-height: 4rem;
        }

        & .skill__symbol {
            width: 11rem;
            height: 11rem;
            background: $main-gradient;
            position: relative;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            transition: all 1s;
            -webkit-transition: all 1s;
            -moz-transition: all 1s;
            -ms-transition: all 1s;
            -o-transition: all 1s;
            backface-visibility: hidden;
            -webkit-backface-visibility: hidden;
            box-shadow: .5rem .5rem 1rem .5rem rgba(0, 0, 0, 0.3);
            margin: auto;
    
            & i, 
            & .img__container {
                font-size: 5rem;
                background: $app-boxes;
                text-align: center;
                line-height: 10rem;
                width: 10rem;
                height: 10rem;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
    
                & {
                    &.fa-code{
                        color: seagreen;
                    }
                    &.fa-html5{
                        color: #f58556;
                    }
                    &.fa-css3-alt{
                        color: #3A94F6;
                    }
                    &.fa-js{
                        color: #f7df1e;
                    }
                    &.fa-node{
                        color: #68a063;
                    }
                    &.fa-react{
                        color: #61dbfb;
                    }
                    &.fa-sass{
                        color: #cc6699;
                    }
                    &.fa-database {
                        color: #e4af01;
                    }
                    &.fa-envelope-open-text {
                        color: #0fc286;
                    }
    
                }
            }
    
            & img {
                width: 8rem;
                height: 8rem;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
            }
        }
    }

    & .skill__text {
        width: 80%;
        padding: 0;
        margin: 0;
        display: inline-block;
    }

    & .skill__description {
        text-align: justify;
        font-size: 1.8rem;
        font-weight: 400;
        padding: 0;
        height: fit-content;
        width: 100%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;


        & li {
            padding: 0 3rem;
            margin: .5rem;
            list-style: none;
        }

        @include respond (phone) {
            & {
              height: fit-content;
              width: 80%;

              & li {
                padding: 0 1rem;
              }
            }
        }
    }
}

.skill__container-mobile {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    width: 90%;
    background: $app-boxes;
    border-radius: 1rem;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    border: 2px solid $light-gray;
    padding: .5rem;
    margin: 0 auto 1.5rem auto;
    max-height: 3rem;
    overflow: hidden;
    transition: all 1s;
    -webkit-transition: all 1s;
    -moz-transition: all 1s;
    -ms-transition: all 1s;
    -o-transition: all 1s;

    &.open {
        max-height: 60vh;
    }

    & .skill__mobile-display {
        display: flex;
        width: 100%;
        flex-flow: row nowrap;
        justify-content: space-between;
        height: 3rem;
        margin-bottom: 1rem;

        & h2 {
            margin: 0 0 1rem 0;
            font-size: 2rem;
            line-height: 3rem;
        }

        & i {
            font-size: 3rem;
        }
    }    

    & .skill__symbol-container {
        height: fit-content;       

        & .skill__symbol {
            width: 10rem;
            height: 10rem;
            background: $main-gradient;
            position: relative;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            box-shadow: .5rem .5rem 1rem .5rem rgba(0, 0, 0, 0.3);

    
            & i, 
            & .img__container {
                font-size: 5rem;
                background: $app-boxes;
                text-align: center;
                line-height: 9rem;
                width: 9rem;
                height: 9rem;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
    
                & {
                    &.fa-code{
                        color: seagreen;
                    }
                    &.fa-html5{
                        color: #f58556;
                    }
                    &.fa-css3-alt{
                        color: #3A94F6;
                    }
                    &.fa-js{
                        color: #f7df1e;
                    }
                    &.fa-node{
                        color: #68a063;
                    }
                    &.fa-react{
                        color: #61dbfb;
                    }
                    &.fa-sass{
                        color: #cc6699;
                    }
                    &.fa-database {
                        color: #e4af01;
                    }
                    &.fa-envelope-open-text {
                        color: #0fc286;
                    }
    
                }
            }
    
            & img {
                width: 7rem;
                height: 7rem;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
            }
        }
    }

    & .skill__text {
        margin-top: 1rem;
        overflow-y: scroll;
        scrollbar-width: none;
        -ms-overflow-style: none;
    
        &::-webkit-scrollbar {
            display: none;
        }
    }

    & .skill__description {        
        padding: 0 2rem 0 2rem;

        & li {
           text-align: justify;
           padding: .5rem 0 0 0;
           margin: 0;
           list-style: none;
        }
    }
}